import { createContext, useState, useEffect } from "react"
import { Route, Routes, Link, useNavigate, Outlet } from 'react-router-dom'
import classNames from 'classnames/bind'
import Calendar from 'react-calendar';
import moment from 'moment';
import Swal from 'sweetalert2'
import axios from 'axios'
import 'react-calendar/dist/Calendar.css';
import style from './todo.module.css'
import '../App.css'

const cs = classNames.bind(style);

function Todo() {

    const [value, onChange] = useState(new Date());

    let [none, setNone] = useState('none')
    let [block, setBlock] = useState('block')

    let [memoData, setMemoData] = useState('') // 

    let [re, setRe] = useState(1)

    let [token, setToken] = useState(localStorage.getItem('token'))
    // console.log(token);




    let [filter, setFilter] = useState([]) //    

    let [DBdate, setDBDate] = useState([]) // DB   title  


    // DB   


    // useEffect(() => {
    //     axios.get('https://todo-project-366420.du.r.appspot.com/list-confirm')

    //         .then(res => {

    //             //    title  
    //             const DBfilter = res.data.filter((data) => {
    //                 return data.title === moment(value).format("YYYY MM DD")
    //             })
    //             let list = DBfilter.map(a => a.name)
    //             setFilter(DBfilter)

    //             let day = res.data.map(a => a.title);
    //             setDBDate(day)

    //         })

    //         .catch(() => {
    //             console.log("");
    //         });

    // }, [moment(value).format("YYYY MM DD"), block, re])

    useEffect(() => {
        axios.post('https://todo-project-366420.du.r.appspot.com/list-confirm', {
            local_id : token
        })

            .then(res => {

                //    title  
                const DBfilter = res.data.filter((data) => {
                    return data.title === moment(value).format("YYYY MM DD")
                })
                let list = DBfilter.map(a => a.name)
                setFilter(DBfilter)

                let day = res.data.map(a => a.title);
                setDBDate(day)

            })

            .catch(() => {
                console.log("");
            });

    }, [moment(value).format("YYYY MM DD"), block, re])

    // console.log(filter);


    //  
    function list_add() {
        let val = document.getElementById('add-input').value
        let id = filter.map(a => a.name)
        axios.post('https://todo-project-366420.du.r.appspot.com/todolist', {
            local_id : token,
            title: moment(value).format("YYYY MM DD"),
            name: val
        })
            .then(function (res) {
                console.log(res.data);
                setBlock('block')
                setNone('none')
            })
            .catch(() => {
                console.log("");
            });
        document.getElementById('add-input').value = ''
    }

    //  
    function list_delete(i) {
        let id = filter[i]._id
        axios.delete('https://todo-project-366420.du.r.appspot.com/list-delete', {
            local_id : token,
            data: { _id: id }
        })
            .then(function (res) {
                setRe(re + 1)
                setMemoData('')
            })
            .catch(() => {
                console.log("");
            });
    }


    //   
    function list_memo(i) {
        let id = filter[i]._id
        let memobox = 'memo' + i
        let memo = document.getElementById(memobox).value

        axios.put('https://todo-project-366420.du.r.appspot.com/list-update', {
            local_id : token,
            id: id,
            memo: memo
        })
            .then(function (res) {
                console.log(res);
                console.log(res.data);
                setRe(re + 1)
                axios.post('https://todo-project-366420.du.r.appspot.com/list-confirm', {
                    local_id : token
                })

                    .then(res => {
                        console.log(res.data);
                        //    title  
                        const DBfilter = res.data.filter((data) => {
                            return data.memo === memo
                        })
                        DBfilter.map((m) => { setMemoData(m) })
                    })

                    .catch(() => {
                        console.log("");
                    });

            })
            .catch(() => {
                console.log("");
            });

        document.querySelector(`.text-container${i}`).style.display = 'none'
    }


    return (
        <>
            <div className={cs('todo-container')}>

                <div className={cs("container1")}>

                    <div className={cs("todo")}>
                        <div className={cs("todo-header")}>

                            <h1>To Do List</h1>
                            <h2>{moment(value).format("YYYY MM DD")} </h2>
                        </div>

                        <div className={cs("todo-main")}>
                            {
                                filter != ''
                                    ?
                                    filter.map((a, i) => {
                                        let list_name = filter[i].name
                                        // let text_memo
                                        let id = filter[i]._id
                                        let text_input_id = 'text-input' + i
                                        let memobox = 'memo' + i

                                        return (
                                            <>
                                                <div className={cs("main-list")}>
                                                    {/*     */}
                                                    <p data-id={id} onClick={(e) => {
                                                        setMemoData(a)
                                                    }}>{i + 1}. {list_name}</p>

                                                    <div className={cs("list-icon")}>

                                                        <i class="fa-solid fa-pen" onClick={() => {
                                                            setRe(re + 1)
                                                            console.log(filter[i].memo);
                                                            document.querySelector(`.text-container${i}`).style.display = 'block'
                                                            document.getElementById(text_input_id).value = list_name //   
                                                            if (filter[i].memo != undefined) {
                                                                document.getElementById(memobox).value = filter[i].memo
                                                            }
                                                            console.log(list_name);

                                                        }}></i>

                                                        {/*   */}
                                                        <i class="fa-solid fa-trash" onClick={() => {
                                                            list_delete(i)
                                                        }}></i>
                                                    </div>
                                                </div>


                                                {/*   */}

                                                <div className={cs(`text-container${i}`, 'text-container')}>
                                                    <div className={cs("text-modal")}>
                                                        <p>{list_name}</p>
                                                        <div className={cs("text-box")}>
                                                            <input id={text_input_id} type='text' />
                                                            <textarea id={memobox} placeholder='  .'></textarea>
                                                        </div>

                                                        <div className={cs("text-btn")}>
                                                            <button onClick={() => {
                                                                list_memo(i)
                                                            }}></button>

                                                            <button onClick={() => {
                                                                document.querySelector(`.text-container${i}`).style.display = 'none'
                                                            }}></button>
                                                        </div>
                                                    </div>
                                                </div>


                                            </>
                                        )
                                    })
                                    : ''
                            }
                        </div>


                        {/*   */}

                        <div className={cs("add")}>

                            <div className={cs("add-input", `${none}`)}>
                                <input id="add-input" type='text' />

                                {/*    */}
                                <button onClick={() => {
                                    let val = document.getElementById('add-input').value
                                    if (val == '') {
                                        Swal.fire('  .')
                                    } else if (val != '') {
                                        list_add()
                                        Swal.fire('  .')

                                    }
                                }}></button>
                            </div>

                            <button className={cs("add-btn", `${block}`)} onClick={() => {
                                setBlock('none')
                                setNone('block')
                            }}><i class="fa-solid fa-plus"></i></button>
                            <button className={cs("close-btn", `${none}`)} onClick={() => {
                                setBlock('block')
                                setNone('none')
                            }}><i class="fa-solid fa-xmark"></i></button>
                        </div>
                    </div>

                </div>



                <div className={cs("container2")}>

                    {/*  */}

                    <div className={cs("todo-calendar")}>
                        <Calendar className={cs('calendar')} onClickDay={() => { setMemoData([]) }} onChange={onChange} value={value}
                            tileContent={({ date, view }) => {
                                if (DBdate.find((x) => x === moment(date).format("YYYY MM DD"))) {
                                    return (
                                        <>
                                            <div className={cs('mark')}>
                                                <div className={cs("dot")}></div>
                                            </div>
                                        </>
                                    );
                                }
                            }} />
                    </div>

                    {/*   */}

                    <div className={cs("memo")}>
                        {
                            memoData != ''
                                ?
                                <div className={cs("none-memo")}>
                                    <h1>{memoData.title}</h1>
                                    <div className={cs("memo-list")}>
                                        <p>{memoData.name}<br /></p>
                                        {
                                            // memoData.memo != 
                                            memoData.memo != undefined
                                                ?
                                                memoData.memo.split('\n').map((line) => {
                                                    return (
                                                        <span>
                                                            {line}
                                                            <br />
                                                        </span>
                                                    )
                                                })
                                                : null
                                        }
                                    </div>

                                </div>
                                : <h1 style={{ textAlign: "center", marginTop: "20px" }}>   .</h1>
                        }

                    </div>

                </div>

            </div>
        </>
    )
}

export default Todo